<template>
	<div class="page-hero" v-if="model">
		<div class="topbar bg-black py-2 px-4 d-flex ai-center">
			<img src="../assets/image/logo.png" height="30" />
			<div class="px-2 flex-1 text-white">
				<span class="">王者荣耀</span>
				<span class="ml-2">攻略战</span>
			</div>
			<router-link :to="`/`" tag="div" class="text-white">更多英雄 &gt;</router-link>
		</div>
		<div class="hero-banner" :style="{ 'background-image': `url(${model.banner})` }">
			<div class="info text-white p-3 d-flex flex-column h-100 jc-end">
				<div class="fx-sm">{{ model.title }}</div>
				<h2 class="my-2">{{ model.name }}</h2>
				<div class="fx-sm">{{ model.categories.map(v => v.name).join('/') }}</div>
				<div class="d-flex jc-between pt-2">
					<div class="scores d-flex ai-center" v-if="model.scores">
						<span>难度：</span>
						<span class="badge bg-primary">{{ model.scores.difficult }}</span>

						<span>技能：</span>
						<span class="badge bg-info ">{{ model.scores.skills }}</span>

						<span>攻击：</span>
						<span class="badge bg-red">{{ model.scores.attack }}</span>

						<span>生存：</span>
						<span class="badge bg-grey">{{ model.scores.survive }}</span>
					</div>
					<router-link to="" tag="span" class="text-grey">皮肤：2 &gt;</router-link>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	props: {
		id: { required: true }
	},
	data() {
		return {
			model: null
		};
	},
	watch: {
		id: 'fetch'
	},
	methods: {
		async fetch() {
			const res = await this.$http.get(`heroes/${this.id}`);
			this.model = res.data;
		}
	},
	created() {
		this.fetch();
	}
};
</script>
<style lang="scss">
.hero-banner {
	height: 50vw;
	background: #fff no-repeat top center;
	background-size: auto 100%;
}

.info {
	background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
	.scores {
		.badge {
			display: inline-block;
			width: 1rem;
			height: 1rem;
			margin: 0 0.25rem;
			line-height: 0.95rem;
			border-radius: 2rem;
			text-align: center;
			font-size: 0.05rem;
			border: 1px solid #999;
		}
	}
}
</style>
